// src/components/AccessDenied.js
import React, { useState, useEffect } from 'react';
import { MessageBox, BusyIndicator } from '@ui5/webcomponents-react';

function getSessionStorageSafe() {
  if (typeof window === 'undefined') return null;
  try {
    return window.top?.sessionStorage || window.sessionStorage;
  } catch (e) {
    return window.sessionStorage;
  }
}

export default function AccessDenied({ user, Auth, adminEmail }) {
  const [showModal, setShowModal] = useState(false);
  const [showSuccessBox, setShowSuccessBox] = useState(false);
  const [alreadyRequested, setAlreadyRequested] = useState(false);

  //    
  const [showPrivacyBox, setShowPrivacyBox] = useState(false);
  const [consent, setConsent] = useState(false);

  const [errorBox, setErrorBox] = useState({ open: false, message: '' });
  const [cooldownBox, setCooldownBox] = useState({ open: false, message: '' });

  //     Busy Indicator
  const [sending, setSending] = useState(false);

  const name = user?.name || '';
  const email = user?.email || user?.id || '';

  //    "    "  
  const storageKey = email ? `workhub.noauth.requested.${email}` : null;

  //   ,       
  useEffect(() => {
    if (!storageKey) return;
    const ss = getSessionStorageSafe();
    if (!ss) return;

    try {
      const flag = ss.getItem(storageKey);
      if (flag === '1') {
        setAlreadyRequested(true);
      }
    } catch (e) {
      console.warn('[AccessDenied] sessionStorage  :', e);
    }
  }, [storageKey]);

  const handleOpenModal = () => {
    if (alreadyRequested) return;
    setShowModal(true);
  };

  const handleCloseModal = () => {
    if (sending) return;
    setShowModal(false);
  };

  //    +  /  
  async function sendRequest() {
    if (!email) {
      setErrorBox({
        open: true,
        message: '          .'
      });
      return;
    }

    if (!consent) {
      setErrorBox({
        open: true,
        message: '        .'
      });
      return;
    }

    //     Busy 
    setSending(true);
    try {
      const res = await Auth.base.call('RequestAccessMail', {
        email: user.id,
        name: user.name
      });

      //  
      if (res && res.ok) {
        if (storageKey) {
          const ss = getSessionStorageSafe();
          ss?.setItem(storageKey, '1');
        }

        setAlreadyRequested(true);
        setShowModal(false);
        setShowSuccessBox(true);
        return;
      }

      // COOLDOWN
      if (res && res.code === 'COOLDOWN') {
        setAlreadyRequested(true);
        setShowModal(false);
        setCooldownBox({
          open: true,
          message: res.message || '   .'
        });
        return;
      }

      //    
      setShowModal(false);
      setErrorBox({
        open: true,
        message: res?.message || '    .'
      });
    } catch (e) {
      console.error('  :', e);
      setErrorBox({
        open: true,
        message: '      .'
      });
    } finally {
      setSending(false);
    }
  }

  const handleSuccessClose = () => {
    setShowSuccessBox(false);
  };

  //   disabled :  ,   ,   
  const disabled = alreadyRequested || !consent || sending;
  const sendButtonStyle = {
    border: 'none',
    borderRadius: '8px',
    padding: '0.6rem 1.4rem',
    fontSize: '0.95rem',
    cursor: disabled ? 'not-allowed' : 'pointer',
    background: '#0a6ed1',
    color: 'white',
    opacity: disabled ? 0.5 : 1
  };

  return (
    <>
      {/*    */}
      {showSuccessBox && (
        <MessageBox
          open={showSuccessBox}
          onClose={handleSuccessClose}
          type="Success"
          titleText="  "
        >
          WorkHub     .
        </MessageBox>
      )}

      {/*   */}
      {errorBox.open && (
        <MessageBox
          open={errorBox.open}
          type="Error"
          titleText=" "
          onClose={() => setErrorBox({ open: false, message: '' })}
        >
          {errorBox.message}
        </MessageBox>
      )}

      {/* COOLDOWN  */}
      {cooldownBox.open && (
        <MessageBox
          open={cooldownBox.open}
          type="Warning"
          titleText=" "
          onClose={() => setCooldownBox({ open: false, message: '' })}
        >
          {cooldownBox.message}
        </MessageBox>
      )}

      {/*     */}
      {showPrivacyBox && (
        <MessageBox
          open={showPrivacyBox}
          type="Information"
          titleText=" "
          onClose={() => setShowPrivacyBox(false)}
        >
          <div style={{ textAlign: 'left', lineHeight: 1.5, fontSize: '0.9rem' }}>
            <p>
              WorkHub        .
            </p>
            <ul style={{ paddingLeft: '1.2rem' }}>
              <li> : ,  </li>
              <li> : WorkHub      </li>
              <li> :            </li>
            </ul>
            <p>
                    ,
                      .
            </p>
          </div>
        </MessageBox>
      )}

      <div
        style={{
          fontFamily:
            '"72", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif',
          background: '#f5f6f8',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          height: '100vh',
          margin: 0
        }}
      >
        <div
          style={{
            background: 'white',
            padding: '2rem 3rem',
            borderRadius: '12px',
            boxShadow: '0 4px 18px rgba(0, 0, 0, 0.08)',
            textAlign: 'center',
            maxWidth: '420px',
            position: 'relative'
          }}
        >
          <h1 style={{ marginTop: 0, marginBottom: '0.75rem', fontSize: '1.6rem' }}>
              
          </h1>
          <p style={{ margin: '0 0 1.5rem', color: '#6a6d70', lineHeight: 1.5 }}>
              WorkHub 
            <br />
            <b> </b> 
            <br />
            <b> </b>  .
          </p>
          <p style={{ margin: '0 0 2rem', color: '#6a6d70', lineHeight: 1.5 }}>
                .
          </p>

          {email && (
            <p style={{ margin: '0 0 0.5rem', color: '#6a6d70' }}>
                : <b>{email}</b>
            </p>
          )}

          {adminEmail && (
            <p style={{ margin: '0 0 2rem', color: '#6a6d70' }}>
                : <b>{adminEmail}</b>
            </p>
          )}

          {alreadyRequested && (
            <p
              style={{
                margin: '0 0 0.8rem',
                color: '#bb0000',
                fontSize: '0.85rem'
              }}
            >
                    .
            </p>
          )}

          {/*    +   */}
          <div
            style={{
              marginTop: '1rem',
              marginBottom: '1rem',
              fontSize: '0.85rem',
              color: '#6a6d70',
              textAlign: 'left'
            }}
          >
            <div style={{ margin: '1.5rem 0 0.5rem' }}>
              <span
                style={{
                  textDecoration: 'underline',
                  color: '#0a6ed1',
                  cursor: 'pointer'
                }}
                onClick={() => setShowPrivacyBox(true)}
              >
                 
              </span>{' '}
               ,   .
            </div>
            <label
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                gap: '0.4rem'
              }}
            >
              <input
                type="checkbox"
                checked={consent}
                onChange={(e) => setConsent(e.target.checked)}
                disabled={sending}
              />
              <span>  .</span>
            </label>
          </div>

          <button
            style={sendButtonStyle}
            onClick={handleOpenModal}
            disabled={disabled}
          >
              
          </button>

          {/*  /  */}
          {showModal && (
            <div
              style={{
                position: 'fixed',
                inset: 0,
                backgroundColor: 'rgba(0, 0, 0, 0.45)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                zIndex: 9999
              }}
            >
              <div
                style={{
                  background: 'white',
                  borderRadius: '12px',
                  padding: '1.5rem 2rem',
                  maxWidth: '420px',
                  width: '90%',
                  boxShadow: '0 4px 18px rgba(0, 0, 0, 0.18)',
                  position: 'relative',
                  overflow: 'hidden'
                }}
              >
                {/*         + BusyIndicator */}
                {sending && (
                  <div
                    style={{
                      position: 'absolute',
                      inset: 0,
                      background: 'rgba(255, 255, 255, 0.7)',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      zIndex: 10
                    }}
                  >
                    <BusyIndicator active size="Medium" />
                  </div>
                )}

                <h2
                  style={{
                    marginTop: 0,
                    marginBottom: '0.75rem',
                    fontSize: '1.3rem'
                  }}
                >
                     
                </h2>
                <p
                  style={{
                    margin: '0 0 1rem',
                    color: '#6a6d70',
                    lineHeight: 1.5
                  }}
                >
                        ?
                </p>

                <div
                  style={{
                    background: '#f5f6f8',
                    borderRadius: '8px',
                    padding: '0.8rem 1rem',
                    textAlign: 'left',
                    fontSize: '0.9rem',
                    marginBottom: '1.2rem'
                  }}
                >
                  <div style={{ marginBottom: '0.35rem' }}>
                    <strong>  :</strong>{' '}
                    {name || <span style={{ color: '#a0a3a8' }}>  </span>}
                  </div>
                  <div>
                    <strong>  :</strong>{' '}
                    {email || <span style={{ color: '#a0a3a8' }}>  </span>}
                  </div>
                </div>

                <p
                  style={{
                    margin: '0 0 1.2rem',
                    color: '#6a6d70',
                    fontSize: '0.9rem',
                    lineHeight: 1.5
                  }}
                >
                   : <b>[WorkHub]  </b>
                  <br />
                      
                  <br />
                  <code>WorkHub     .</code>  .
                </p>

                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'flex-end',
                    gap: '0.5rem',
                    marginTop: '1rem'
                  }}
                >
                  <button
                    style={{
                      border: '1px solid #d3d7db',
                      background: 'white',
                      borderRadius: '8px',
                      padding: '0.45rem 1.1rem',
                      fontSize: '0.9rem',
                      cursor: sending ? 'not-allowed' : 'pointer'
                    }}
                    onClick={handleCloseModal}
                    disabled={sending}
                  >
                    
                  </button>
                  <button
                    style={{
                      border: 'none',
                      borderRadius: '8px',
                      padding: '0.45rem 1.1rem',
                      fontSize: '0.9rem',
                      cursor: sending ? 'not-allowed' : 'pointer',
                      background: '#0a6ed1',
                      color: 'white',
                      opacity: sending ? 0.7 : 1
                    }}
                    onClick={sendRequest}
                    disabled={sending}
                  >
                    {sending ? ' ...' : ' '}
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
}

